  width: 100%;
  height: 40px;
  letter-spacing: .01em;
  color: #212121;
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  margin: 0;
  padding-left: 42px;
  padding-right: 16px;
  font-size: 14px;
  line-height: 1.14;
  transition: border .25s cubic-bezier(.4, 0, .2, 1);
}

.search-form__input:focus-visible {
  outline: 1px solid #2196f3;
